<template>
  <div class="latest-race">
    <!-- If qualifying and race data available -->
    <!-- Display Header, Race info, and Qualifying data -->
    <template v-if="qualifying.round === race.round && qualifying.season === race.season">
      <RaceHeader :seasons="seasons" :race="race" :raceDataIsFetching="raceDataIsFetching"></RaceHeader>
      <template>
        <RaceInformation
          v-if="!raceDataIsFetching"
          :raceDataIsFetching="raceDataIsFetching"
          :race="race"
        ></RaceInformation>
      </template>
      <RaceResults :raceResults="raceResults" :racePitStops="racePitStops"></RaceResults>
    </template>
    <!-- If qualifying data available but no race -->
    <!-- Display Header, Race info, Race data, and Qualifying data -->
    <template v-else>
      <RaceHeader
        :seasons="seasons"
        :race="qualifying"
        :raceDataIsFetching="qualifyingDataIsFetching"
      ></RaceHeader>
      <RaceInformation
        v-if="!qualifyingDataIsFetching"
        :raceDataIsFetching="qualifyingDataIsFetching"
        :race="qualifying"
      ></RaceInformation>
    </template>
    <template v-if="qualifyingResults && race && race.Circuit && race.Circuit.Location">
      <QualifyingResults v-if="!qualifyingDataIsFetching" :qualifyingResults="qualifyingResults"></QualifyingResults>
    </template>
  </div>
</template>

<script>
import RaceHeader from '@/components/RaceHeader'
import RaceInformation from '@/components/RaceInformation'
import RaceResults from '@/components/RaceResults'
import QualifyingResults from '@/components/QualifyingResults'
import EventBus from '@/event-bus/event-bus.js'

export default {
  name: 'latest-race',
  props: {
    qualifyingResults: [Array, Boolean],
    racePitStops: [Array, Boolean],
    raceResults: [Array, Boolean],
    race: [Object, Boolean],
    qualifying: [Object, Boolean],
    raceDataIsFetching: Boolean,
    qualifyingDataIsFetching: Boolean,
    seasons: Array
  },
  components: {
    RaceHeader,
    RaceInformation,
    RaceResults,
    QualifyingResults
  },
  created () {
    this.date = new Date()
    this.day = this.date.getDay()
    this.hours = this.date.getHours()

    // Used later to abort web request if it fails
    const controller = new AbortController()

    if ((this.day === 7 && this.hours > 9 && this.hours < 24) || (this.day === 1 && this.hours < 9)) {
      // Use cache if race has just ended
      this.options = { cache: 'no-store', method: 'GET', signal: controller.signal }
    } else {
      this.options = { cache: 'force-cache', method: 'GET', signal: controller.signal }
    }

    // Ergast data
    this.data = {
      results: 'https://ergast.com/api/f1/current/last/results.json',
      qualifying: 'https://ergast.com/api/f1/current/last/qualifying.json',
      // qualifying: 'http://ergast.com/api/f1/2008/5/qualifying.json',
      pitStops: 'https://ergast.com/api/f1/current/last/pitstops.json'
    }

    // Emitted to App.vue
    EventBus.$emit('raceRequest', this.data, this.options, controller)
  }
}
</script>
